import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import styles from './index.css';
import { formatImage } from '@ifeng/public_method';
import { publicPath, devPath } from '@src/constants';
// import { Grounds } from '@ifeng/client_grounds';

class HistoryItem extends PureComponent {
    static propTypes = {
        id: PropTypes.string,
        videoSeriesId: PropTypes.string,
        index: PropTypes.number,
        title: PropTypes.string, // 标题
        image: PropTypes.string, // 标题
        summary: PropTypes.string, // 简介
        totalTime: PropTypes.string, // 总时间
        currentTime: PropTypes.string, // 观看到的时间点
        watchTime: PropTypes.string, // 观看时间
        isHistory: PropTypes.bool, // 是否为历史记录
    };

    static defaultProps = {
        isHistory: true,
    };

    getProgressPercent = () => {
        const { totalTime, currentTime } = this.props;
        let totalSeconds = 0;
        let currentSeconds = 0;

        const totalArr = totalTime.split(':');
        const currentArr = currentTime.split(':');

        if (totalArr.length === 2) {
            const [totalMinute, totalSecond] = totalArr;

            totalSeconds = Number(totalMinute) * 60 + Number(totalSecond);
        } else if (totalArr.length === 3) {
            const [totalHour, totalMinute, totalSecond] = totalArr;

            totalSeconds = Number(totalHour) * 60 * 60 + Number(totalMinute) * 60 + Number(totalSecond);
        }

        if (currentArr.length === 2) {
            const [currentMinute, currentSecond] = currentArr;

            currentSeconds = Number(currentMinute) * 60 + Number(currentSecond);
        } else if (currentArr.length === 3) {
            const [currentHour, currentMinute, currentSecond] = currentArr;

            currentSeconds = Number(currentHour) * 60 * 60 + Number(currentMinute) * 60 + Number(currentSecond);
        }

        return `${(currentSeconds / totalSeconds) * 100}%`;
    };

    toDetail = () => {
        const { id, videoSeriesId, index, isHistory } = this.props;
        const finalUrl = `${devPath}/details/${id}?${
            isHistory ? `positiveId=${videoSeriesId}&source=positive&` : ''
        }id=phmovie_${id}&type=phmovie&ref=history&rnum=${index}&showtype=phhistory&isFull=0&immersion=true&needpage=1`;

        // const finalUrl = `${'http://172.30.20.17:3000/mobile/phtv/loc/tvTheater'}/details/${id}?${
        //     isHistory ? `positiveId=${videoSeriesId}&source=positive&` : ''
        // }id=phmovie_${id}&type=phmovie&ref=history&rnum=${index}&showtype=phhistory&isFull=0&immersion=true&needpage=1`;

        if (window.grounds) {
            Grounds.dispatch({
                link: {
                    type: 'web',
                    url: finalUrl,
                },
                pageLogParams: {
                    id: `phmovie_${id}`,
                    ref: 'history',
                    rnum: index,
                    showtype: 'phhistory',
                    type: 'phmovie',
                },
            });
        } else {
            window.location.href = finalUrl;
        }
    };

    render() {
        const { title, image, summary, totalTime, currentTime, watchTime, isHistory } = this.props;

        return (
            <div className={styles.historyItem} onClick={this.toDetail}>
                <div className={styles.imagePart}>
                    <img src={formatImage(image, 264, 148)} alt="" />
                    <div className={styles.mask}>
                        <div className={styles.duration}>
                            {currentTime ? `${currentTime}/` : ''}
                            {totalTime}
                        </div>
                        {isHistory && (
                            <div className={styles.progressBar}>
                                <div className={styles.progressThumb} style={{ width: this.getProgressPercent() }} />
                            </div>
                        )}
                    </div>
                </div>
                <div className={styles.textPart}>
                    <div className={styles.title}>{title}</div>
                    <div className={styles.subTitle}>
                        {isHistory ? '上次看到' : ''}
                        <span className={`${isHistory ? styles.history : ''}`}>{summary}</span>
                    </div>
                    <div className={styles.time}>
                        <img src={require('@src/common/assets/time.png')} alt="" />
                        {watchTime}
                    </div>
                </div>
            </div>
        );
    }
}

export default HistoryItem;
