import React, { useEffect, useState } from 'react';
import PropTypes from 'prop-types';
import styles from '@src/mobile/history/layout/index.css';
import dayjs from 'dayjs';
import HistoryItem from '@src/mobile/history/layout/HistoryItem';
import { i2s } from '@src/utils/format';
import { dialog } from '@src/common/components/ModalBox';
import nzhcn from 'nzh/cn';
// import { Grounds } from '@ifeng/client_grounds';

const History = props => {
    const { changeEmpty, isEdit, changeEdit } = props;

    const [isEmpty, setIsEmpty] = useState(false); // 渲染的数据列表是否为空
    const [originHistory, setOriginHistory] = useState([]); // 原始的 localStorage 里面的数据
    const [historyList, setHistoryList] = useState([]); // 渲染的数据。二维数组，分为【今天】、【昨天】和【更早】
    const [selectedList, setSelectedList] = useState([]); // 选择列表

    useEffect(() => {
        getHistory();
        setPageActiveListener(getHistory);
    }, []);

    const getHistory = () => {
        const list = JSON.parse(localStorage.getItem('history'));

        const today = [];
        const yesterday = [];
        const moreBefore = [];

        if (list) {
            list.sort((a, b) => (b.createTime > a.createTime ? 1 : -1));
            list.forEach(item => {
                item.videoSeries.sort((a, b) => (new Date(b.createTime) > new Date(a.createTime) ? 1 : -1));
                item.videoSeries = item.videoSeries.filter(v => v.type !== 'gags');
                if (dayjs().isSame(dayjs(item.videoSeries[0].createTime), 'day')) {
                    // 今天
                    today.push(item);
                } else if (
                    dayjs()
                        .subtract(1, 'day')
                        .isSame(dayjs(item.videoSeries[0].createTime), 'day')
                ) {
                    // 昨天
                    yesterday.push(item);
                } else if (
                    dayjs()
                        .subtract(1, 'month')
                        .isBefore(dayjs(item.videoSeries[0].createTime), 'day')
                ) {
                    // 一个月以内的，按天算
                    moreBefore.push(item);
                }
            });
        }

        const isEmpty = today.concat(yesterday).concat(moreBefore).length < 1;

        setIsEmpty(isEmpty);
        changeEmpty(isEmpty);
        setOriginHistory(list);
        setHistoryList([today, yesterday, moreBefore]);
    };

    const setPageActiveListener = callback => {
        // 安卓 webview 无法监听 visibilitychange 事件
        const isAndroid = window.grounds ? /android/i.test(Grounds.getDeviceInfo().getOs()) : false;

        if (window.grounds && isAndroid) {
            Grounds.on('active', 'historyTask_reGetData');
            window.historyTask_reGetData = () => callback();
        } else {
            document.addEventListener('visibilitychange', () => {
                if (document.visibilityState === 'visible') {
                    callback();
                }
            });
        }
    };

    const handleSelect = e => {
        const { id } = e.currentTarget.dataset;

        if (selectedList.includes(id)) {
            const newList = [...selectedList];

            newList.splice(newList.findIndex(a => a === id), 1);
            setSelectedList(newList);
        } else {
            setSelectedList([...selectedList, id]);
        }
    };

    const selectAll = () => {
        const list = [];

        if (!isAll()) {
            historyList.forEach(a => {
                a.forEach(b => {
                    list.push(b.id);
                });
            });
        }

        setSelectedList(list);
    };

    const isAll = () => {
        let allNums = 0;

        historyList.reduce((a, b) => (allNums += (a.length || 0) + (b.length || 0)));

        return selectedList.length === allNums;
    };

    const deleteConfirm = () => {
        if (selectedList.length < 1) {
            return;
        }
        dialog({
            content: <div style={{ textAlign: 'center' }}>确认删除选中内容吗？</div>,
            onOk: async () => {
                let newList = [...originHistory];

                newList = newList.filter(a => !selectedList.includes(a.id));
                localStorage.setItem('history', JSON.stringify(newList));
                setSelectedList([]);
                changeEdit();
                getHistory();
            },
            okText: '确认',
            cancelText: '取消',
        });
    };

    return (
        <div className={`${styles.content} ${isEdit ? styles.edit : ''}`}>
            {isEmpty ? (
                <div className={styles.empty}>
                    <img src={require('@src/common/assets/history_empty.png')} alt="空" />
                </div>
            ) : (
                <>
                    {historyList.map((history, index) => {
                        if (history.length > 0) {
                            return (
                                <div className={styles.timePart} key={index}>
                                    <div className={styles.timeTitle}>
                                        {/* eslint-disable-next-line no-nested-ternary */}
                                        {index === 0 ? '今天' : index === 1 ? '昨天' : '更早'}
                                    </div>
                                    {history.map((item, i) => {
                                        const first = item.videoSeries[0];
                                        const createTime = dayjs(item.videoSeries[0].createTime).format('MM-DD HH:mm');

                                        return (
                                            <div className={styles.timeItem} key={`${index}-${i}`}>
                                                {isEdit && (
                                                    <div
                                                        className={`${styles.checkBox} ${
                                                            selectedList.includes(item.id) ? styles.checked : ''
                                                        }`}
                                                        data-id={item.id}
                                                        onClick={handleSelect}
                                                    />
                                                )}
                                                <HistoryItem
                                                    id={item.id}
                                                    videoSeriesId={first.id}
                                                    index={i}
                                                    title={item.title}
                                                    image={item.posterUrl}
                                                    summary={
                                                        item.extText
                                                            ? item.extText
                                                            : `第${nzhcn.encodeS(Number(item.currentIndex) + 1)}集`
                                                    }
                                                    currentTime={i2s(first.currentTime)}
                                                    totalTime={first.duration}
                                                    watchTime={createTime}
                                                />
                                            </div>
                                        );
                                    })}
                                </div>
                            );
                        } else {
                            return null;
                        }
                    })}
                    {!isEdit && historyList.length > 0 && <div className={styles.tip}>—— 展示最近一个月记录 ——</div>}
                    {isEdit && (
                        <div className={styles.operation}>
                            <div onClick={selectAll}>{isAll() ? '取消全选' : '全选'}</div>
                            <div
                                onClick={deleteConfirm}
                                className={`${styles.confirm} ${selectedList.length > 0 ? styles.active : ''}`}>
                                删除
                                {selectedList.length > 0 && <span>({selectedList.length})</span>}
                            </div>
                            <span className={styles.line} />
                        </div>
                    )}
                </>
            )}
        </div>
    );
};

History.propTypes = {
    changeEmpty: PropTypes.func,
    isEdit: PropTypes.bool,
    changeEdit: PropTypes.func,
};

export default History;
