import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import dayjs from 'dayjs';
import { dialog } from '@src/common/components/ModalBox';
import styles from '@src/mobile/history/layout/index.css';
import HistoryItem from '@src/mobile/history/layout/HistoryItem';
import { GetCollectList } from '@src/mobile/history/services';
// import LoadingMore from '@src/components/LoadingMore';
// import { Grounds } from '@ifeng/client_grounds';

const Collect = (props) => {
    const { changeEmpty, isEdit, changeEdit } = props;

    const [ guid, setGuid ] = useState('');
    const [ pageIndex, setPageIndex ] = useState(1);
    const [ isEmpty, setIsEmpty ] = useState(false); // 渲染的数据列表是否为空
    const [ collectList, setCollectList ] = useState([]); // 渲染的数据。二维数组，分为【今天】、【昨天】和【更早】
    const [ selectedList, setSelectedList ] = useState([]); // 选择列表
    const [ rows, setRows ] = useState(0);
    const [ isLogin, setIsLogin ] = useState(-1); // 是否登录 -1：初始 0：未登录 1：登录

    useEffect(() => {
        getCollect();
        setPageActiveListener(getCollect);
    }, []);

    const getCollect = async () => {
        if (!window.grounds) {
            setCollectList([]);
            setIsLogin(0);

            return;
        }

        const guid = Grounds.getUserInfo().getGuid();

        if (guid) {
            setIsLogin(1);
        } else {
            setIsLogin(0);
        }

        setGuid(guid);
        const res = await GetCollectList({ guid, page: pageIndex });
        
        const list = res.list || [];

        setPageIndex(pageIndex + 1);
        setRows(res.listRows);
        setCollectList(list);

        setIsEmpty(list.length === 0);
        changeEmpty(list.length === 0);
    };

    const setPageActiveListener = callback => {
        // 安卓 webview 无法监听 visibilitychange 事件
        const isAndroid = window.grounds ? /android/i.test(Grounds.getDeviceInfo().getOs()) : false;

        if (window.grounds && isAndroid) {
            Grounds.on('active', 'historyTask_reGetData');
            window.historyTask_reGetData = () => callback();
        } else {
            document.addEventListener('visibilitychange', () => {
                if (document.visibilityState === 'visible') {
                    callback();
                }
            });
        }
    };

    const handleSelect = (e) => {
        const { docid } = e.currentTarget.dataset;

        if (selectedList.includes(docid)) {
            const newList = [...selectedList];

            newList.splice(newList.findIndex(a => a === docid), 1);
            setSelectedList(newList);
        } else {
            setSelectedList([ ...selectedList, docid ]);
        }
    };

    const selectAll = () => {
        const list = [];

        if (!isAll()) {
            collectList.forEach(a => {
                list.push(a.docid);
            });
        }

        setSelectedList(list);
    };

    const isAll = () => {
        return selectedList.length === collectList.length;
    };

    const deleteConfirm = () => {
        if (selectedList.length < 1) {
            return;
        }
        dialog({
            content: <div style={{ textAlign: 'center' }}>确认删除选中内容吗？</div>,
            onOk: async () => {
                const cb = 'callBackName';
                const dumy = 'dumy';

                window[dumy] = () => {};
                window[cb] = (result) => {
                    if (result) {
                        const newList = [...collectList];

                        selectedList.forEach(id => {
                            newList.splice(newList.findIndex(a => a.docid === id), 1);
                        });
                        setCollectList(newList);
                        setSelectedList([]);
                        changeEdit();
                        setIsEmpty(newList.length === 0);
                        changeEmpty(newList.length === 0);
                        delete window[cb];
                        delete window[dumy];
                    }
                };

                selectedList.forEach((id, index) => {
                    Grounds.collect(
                        {
                            documentId: id
                        },
                        false,
                        index === selectedList.length - 1 ? cb : dumy
                    );
                });
            },
            okText: '确认',
            cancelText: '取消',
        });
    };

    const control = async () => {
        if (!window.grounds) {
            setCollectList([]);

            return;
        }

        if (pageIndex > rows) {
            return;
        }

        const res = await GetCollectList({ guid, page: pageIndex });
        const list = res.list || [];

        setPageIndex(pageIndex + 1);
        const finalList = [...collectList, ...list];

        setCollectList(finalList);
        setIsEmpty(finalList.length === 0);
        changeEmpty(finalList.length === 0);
    };

    const toLogin = () => {
        Grounds.userLogin();
    };

    return (
        <div className={`${styles.content} ${isEdit ? styles.edit : ''}`}>
            {
                isEmpty || isLogin === 0
                    ?
                    <>
                        <div className={styles.empty}>
                            <img src={require('@src/common/assets/collect_empty.png')} alt="空" />
                            {
                                isLogin === 0 && <span className={styles.loginTip} onClick={toLogin}>请登录后再进行查看哟</span>
                            }
                        </div>
                    </>

                    :
                    <div className={styles.timePart}>
                        {
                            collectList.map((collect, index) => {
                                return (
                                    <div className={styles.timeItem} key={`${index}`}>
                                        {
                                            isEdit && <div className={`${styles.checkBox} ${selectedList.includes(collect.docid) ? styles.checked : ''}`} data-docid={collect.docid} onClick={handleSelect} />
                                        }
                                        <HistoryItem
                                            id={collect.docid}
                                            videoSeriesId={collect.docid}
                                            index={index}
                                            title={collect.title}
                                            image={collect.thumbnail}
                                            summary={collect.subHead}
                                            totalTime={collect.numText}
                                            watchTime={dayjs(collect.ctime * 1000).format('YYYY-MM-DD HH:mm')}
                                            isHistory={false}
                                        />
                                    </div>
                                );
                            })
                        }
                    </div>
            }
            {
                isEdit &&
                <div className={styles.operation}>
                    <div onClick={selectAll}>{isAll() ? '取消全选' : '全选' }</div>
                    <div onClick={deleteConfirm} className={`${styles.confirm} ${selectedList.length > 0 ? styles.active : ''}`}>
                        删除
                        {
                            selectedList.length > 0 && <span>({selectedList.length})</span>
                        }
                    </div>
                    <span className={styles.line} />
                </div>
            }
            {/* {
                pageIndex <= rows &&
                <LoadingMore control={control} />
            } */}
        </div>
    );
};

Collect.propTypes = {
    changeEmpty: PropTypes.func,
    isEdit: PropTypes.bool,
    changeEdit: PropTypes.func
};

export default Collect;
