import React, { useState, useEffect } from 'react';
import styles from './index.css';
import History from './History';
import Collect from './Collect';
// import { Grounds } from '@ifeng/client_grounds';

const Layout = () => {
    const [isEdit, setIsEdit] = useState(false); // 编辑模式
    const [statusBarHeight, setStatusBarHeight] = useState(0); // 状态栏高度
    const [pageIsEmpty, setPageIsEmpty] = useState(false); // 渲染的数据列表是否为空
    const [tabIndex, setTabIndex] = useState(0);

    useEffect(() => {
        init();
        statistics();
    }, []);

    const back = () => {
        if (window.grounds) {
            Grounds.close();
        } else {
            window.history.back();
        }
    };

    const toggleIsEdit = () => {
        setIsEdit(!isEdit);
    };

    const statistics = () => {
        // const params = new URL(window.location.href).searchParams;

        // const ref = params.get('ref') || Grounds.getPageInfo?.()?.getRef?.() || '';
        // const rnum = params.get('rnum') || Grounds.getPageInfo?.()?.getRnum?.() || '';
        // const showtype = params.get('showtype') || Grounds.getPageInfo?.()?.getShowtype?.() || '';
        // const reftype = params.get('reftype');

        // Grounds.pageStat({
        //     behavior: 'page',
        //     id: 'phhistory',
        //     ref,
        //     type: 'history',
        //     reftype,
        //     showtype,
        //     rnum,
        // });
    };

    const init = async () => {
        // 沉浸式
        if (window.grounds && Grounds.maximization && Grounds.getStatusBarHeight) {
            const isAndroid = /android/i.test(Grounds.getDeviceInfo().getOs());
            const ratio = isAndroid ? devicePixelRatio : 1;
            const statusBarHeight = Grounds.getStatusBarHeight() / ratio;

            Grounds.maximization();
            setStatusBarHeight(statusBarHeight);
        }
    };

    const changeTabIndex = e => {
        const { index } = e.currentTarget.dataset;

        setIsEdit(false);
        setTabIndex(+index);
    };

    return (
        <div className={styles.layout} style={{ paddingTop: `${statusBarHeight}px` }}>
            <div className={styles.toolbar}>
                <img onClick={back} src={require('@src/common/assets/back_dark.png')} alt="" />
                <div className={styles.titleWp}>
                    <span
                        className={`${styles.title} ${tabIndex === 0 ? styles.active : ''}`}
                        data-index={0}
                        onClick={changeTabIndex}>
                        观看记录
                    </span>
                    <span
                        className={`${styles.title} ${tabIndex === 1 ? styles.active : ''}`}
                        data-index={1}
                        onClick={changeTabIndex}>
                        剧集收藏
                    </span>
                </div>
                {pageIsEmpty ? (
                    <span />
                ) : (
                    <span onClick={toggleIsEdit} className={`${isEdit ? styles.cancel : styles.edit}`}>
                        {isEdit ? '取消' : '编辑'}
                    </span>
                )}
            </div>
            {tabIndex === 0 ? (
                <History changeEmpty={setPageIsEmpty} isEdit={isEdit} changeEdit={toggleIsEdit} />
            ) : (
                <Collect changeEmpty={setPageIsEmpty} isEdit={isEdit} changeEdit={toggleIsEdit} />
            )}
        </div>
    );
};

export default Layout;
